import type { Device, EventPayload } from './types'

export function saveToLocalStorage(event: EventPayload): void {
    const failedEvents = getFromLocalStorage() || []
    failedEvents.push(event)
    localStorage.setItem('tracking_failed_events', JSON.stringify(failedEvents))
}

export function getFromLocalStorage(): EventPayload[] | null {
    const data = localStorage.getItem('tracking_failed_events')
    return data ? JSON.parse(data) : null
}

export function retryFailedEvents(sendEvent: (event: EventPayload) => void): void {
    const failedEvents = getFromLocalStorage()
    if (!failedEvents || failedEvents.length === 0) return

    failedEvents.forEach(event => sendEvent(event))
    localStorage.removeItem('tracking_failed_events')
}

function parseUserAgent(userAgent: string) {
    const deviceInfo: Record<string, any> = {
        deviceType: 'Unknown'
    }

    // Device type detection
    if (/Mobi|Android/i.test(userAgent)) {
        deviceInfo.deviceType = 'Mobile'
    } else if (/Tablet|iPad/i.test(userAgent)) {
        deviceInfo.deviceType = 'Tablet'
    } else if (/Desktop|Macintosh|Windows/i.test(userAgent)) {
        deviceInfo.deviceType = 'Desktop'
    }

    // Device brand and model detection
    if (/iPhone/i.test(userAgent)) {
        deviceInfo.deviceBrand = 'Apple'
        deviceInfo.deviceModel = 'iPhone'
    } else if (/iPad/i.test(userAgent)) {
        deviceInfo.deviceBrand = 'Apple'
        deviceInfo.deviceModel = 'iPad'
    } else if (/Samsung/i.test(userAgent)) {
        deviceInfo.deviceBrand = 'Samsung'
        const modelMatch = userAgent.match(/SM-[\w\d]+/)
        if (modelMatch) {
            deviceInfo.deviceModel = modelMatch[0]
        }
    } else if (/Huawei/i.test(userAgent)) {
        deviceInfo.deviceBrand = 'Huawei'
    } else if (/Pixel/i.test(userAgent)) {
        deviceInfo.deviceBrand = 'Google'
        deviceInfo.deviceModel = 'Pixel'
    }

    // Operating system detection
    const osPatterns = [
        { name: 'iOS', regex: /CPU (iPhone )?OS ([\d_]+)/ },
        { name: 'Android', regex: /Android ([\d.]+)/ },
        { name: 'Windows', regex: /Windows NT ([\d.]+)/ },
        { name: 'MacOS', regex: /Mac OS X ([\d_]+)/ },
        { name: 'Linux', regex: /Linux/ }
    ]

    for (const os of osPatterns) {
        const match = userAgent.match(os.regex)
        if (match) {
            deviceInfo.os = {
                name: os.name,
                version: match[2]?.replace(/_/g, '.')
            }
            break
        }
    }

    // Browser detection
    const browserPatterns = [
        { name: 'Chrome', regex: /Chrome\/([\d.]+)/ },
        { name: 'Firefox', regex: /Firefox\/([\d.]+)/ },
        { name: 'Safari', regex: /Version\/([\d.]+).*Safari/ },
        { name: 'Edge', regex: /Edg\/([\d.]+)/ },
        { name: 'Opera', regex: /Opera\/([\d.]+)|OPR\/([\d.]+)/ }
    ]

    for (const browser of browserPatterns) {
        const match = userAgent.match(browser.regex)
        if (match) {
            deviceInfo.browser = {
                name: browser.name,
                version: match[1] || match[2]
            }
            break
        }
    }

    // Rendering engine detection
    const enginePatterns = [
        { name: 'Blink', regex: /Chrome\/([\d.]+)/ },
        { name: 'WebKit', regex: /AppleWebKit\/([\d.]+)/ },
        { name: 'Gecko', regex: /Gecko\/([\d.]+)/ }
    ]

    for (const engine of enginePatterns) {
        const match = userAgent.match(engine.regex)
        if (match) {
            deviceInfo.engine = {
                name: engine.name,
                version: match[1]
            }
            break
        }
    }

    return deviceInfo
}

export function getDeviceInfo(id: string): Device {
    const deviceInfo = parseUserAgent(navigator.userAgent)
    return {
        id,
        name: deviceInfo.deviceModel,
        timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
        language: navigator.language,
        os: deviceInfo.os.name,
        osVersion: deviceInfo.os.version,
        type: deviceInfo.deviceType === 'Mobile' ? 'PHONE' : deviceInfo.deviceType,
        width: window.screen.width + '',
        height: window.screen.height + '',
        manufacturer: deviceInfo.deviceBrand || navigator.vendor,
        model: ''
    }
}

export function getSDKInfo() {
    return {
        name: 'oneloyalty-tracking-web-sdk',
        version: '1.0.0'
    }
}
