import { v4 as uuidv4 } from 'uuid'
import { saveToLocalStorage, getDeviceInfo, retryFailedEvents, getSDKInfo } from './utils'
import { AppInformation, Device, EventPayload, SDK } from './types'

interface InitOptions {
    apiKey?: string
    appName: string
    appVersion: string
    baseUrl: string
    deviceId: string
    userId?: string
    debug?: boolean
}

class Tracker {
    private apiKey: string | null = null
    private appInfo: AppInformation | null = null
    private baseUrl: string | null = null
    private debug: boolean = false
    private deviceId: string | null = null
    private deviceInfo: Device | null = null
    private eventQueue: EventPayload[] = []
    private properties = {}
    private sdkInfo: SDK | null = null

    init(options: InitOptions): void {
        const {
            deviceId,
            appName,
            appVersion,
            baseUrl,
            apiKey = 'oneloyalty-app',
            debug = false,
            userId = null
        } = options
        if (!apiKey) {
            console.error('Tracking SDK: Missing apiKey.')
            return
        }

        if (!deviceId || !baseUrl) {
            console.error('Tracking SDK: Missing deviceId or baseUrl.')
            return
        }

        if (!appName || !appVersion) {
            console.error('Tracking SDK: Missing appName or appVersion.')
            return
        }

        this.apiKey = apiKey
        this.appInfo = {
            name: appName,
            version: appVersion
        }
        this.baseUrl = baseUrl
        this.debug = debug
        this.deviceId = deviceId
        this.deviceInfo = getDeviceInfo(deviceId)
        this.properties = { userAgent: navigator.userAgent, userId: userId }
        this.sdkInfo = getSDKInfo()

        if (this.debug) console.log('Tracking SDK initialized:', { deviceId, baseUrl })
        // retryFailedEvents(this.sendEvent.bind(this))
    }

    trackEvent(eventName: string, data: Record<string, any> = {}): void {
        if (!this.apiKey || !this.baseUrl || !this.appInfo || !this.deviceInfo || !this.sdkInfo) {
            console.error('Tracking SDK: SDK not initialized.')
            return
        }

        const payload: EventPayload = {
            id: uuidv4(),
            specVersion: this.sdkInfo.version,
            source: `${this.appInfo.name}-${this.sdkInfo.name}`,
            type: 'EVENT',
            data: {
                deviceId: this.deviceInfo?.id,
                eventName,
                context: {
                    appInformation: this.appInfo,
                    sdk: this.sdkInfo,
                    device: this.deviceInfo
                },
                properties: { ...this.properties, ...data },
                timeRecord: Date.now()
            }
        }

        this.eventQueue.push(payload)
        this.sendEvent(payload)
    }

    private sendEvent(payload: EventPayload): void {
        if (!this.apiKey || !this.deviceId || !this.baseUrl) return

        fetch(`${this.baseUrl}/publish/api/v1/user-events`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'X-Client-Id': `${this.apiKey}`
            },
            body: JSON.stringify({
                limit: 1,
                records: [payload]
            })
        })
            .then(response => {
                if (!response.ok) throw new Error('Network response was not ok')
                return response.json()
            })
            .then(data => {
                if (this.debug) console.log('Event tracked:', data)
                this.eventQueue = this.eventQueue.filter(e => e !== payload)
            })
            .catch(error => {
                console.error('Tracking SDK: Error tracking event:', error)
                saveToLocalStorage(payload)
            })
    }

    setUser(userId: string, data: Record<string, any> = {}): void {
        this.trackEvent('set_user', { userId, ...data })
    }
}

export default Tracker
